import React from 'react';

class Crumbs extends React.Component {
  constructor(props) {
    super(props);
    let val = Storage.get('key');
    let appid = Storage.get('appid');
    if (val == null) {
      val = 1;
    }
    if (appid == null) {
      appid = 100000168;
    }
    Storage.set('key', val);
    Storage.set('appid', appid);
    
    this.state = {
      value: val,
      appId: appid,
      projects: []
    }
  }
  componentDidMount() {
    
  }
  sendAjax(event) {
    let val = event.target.value;
    Storage.set('key', val);
    this.state.projects.map((project,i)=>{
      if(project.id == val){
        Storage.set('appid',project.code);
      }
    });
    window.location.reload();
  }
  render() {
    return (
      <div className="breadcrumbs">
        <ul className="breadcrumb">
          <li>
            <i className="icon-home home-icon"></i>
            <a href="#">首页</a>
          </li>
          <li className="active">
            {this.props.route ? this.props.route.name : ''}
          </li>
        </ul>
        <div className="nav-search" id="nav-search">
        <form className="form-search">
          <select className="form-control"
            onChange={this.sendAjax.bind(this)} value={this.state.value}>
            {
              this.state.projects.map((items, i) => {
                return (<option value={items.id} key={i}>{items.name}</option>)
              })
            }
          </select>
        </form>
      </div>
      </div>
    )
  }
}

export default Crumbs;


